@charset "UTF-8";
/*サイト個別設定*/
@keyframes fadeIn {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@keyframes fadeOut {
  from {
    opacity: 1; }
  to {
    opacity: 0; } }

@keyframes spin {
  0% {
    transform: rotate(0deg); }
  100% {
    transform: rotate(360deg); } }

a {
  color: #6eb3dd; }

.globalHeader {
  /*フォームページ等のタイトル*/ }
  .globalHeader__logo {
    width: 75px; }
    @media all and (min-width: 781px) {
      .globalHeader__logo {
        width: 212px; } }
  .globalHeader__title {
    background-color: #6eb3dd; }

.globalNavi {
  background-color: #6eb3dd;
  color: #fff; }
  .globalNavi__childItem a span {
    color: #6eb3dd; }
  .globalNavi__item--hasChild.-active > span {
    color: #6eb3dd; }
    .globalNavi__item--hasChild.-active > span::after {
      border-top-color: #6eb3dd; }

.container__sideSectionTitle {
  background-color: #6eb3dd; }

.qaList__question {
  color: #6eb3dd; }

.article__title {
  color: #6eb3dd; }

.article__answer {
  /*変える必要があれば*/ }

.button:not(.button--entry):not(.button--green):not(.button--pink) {
  background-color: #6eb3dd;
  border-bottom-color: #2c83ba; }

.qaData__itemHeadline .qIcon {
  color: #6eb3dd; }

.qaData__itemValue em {
  color: #6eb3dd; }

.searchForm__inner {
  background-color: #deeef7; }

.searchForm__form input {
  border-color: #6eb3dd; }

.searchForm__form button {
  background-color: #6eb3dd; }

.postQuestion {
  border-color: #6eb3dd; }

.glossaryNavi__item a::after {
  background-color: #6eb3dd; }

.glossaryList__catTitle {
  color: #6eb3dd; }

.glossaryList__word a::before {
  border-left-color: #6eb3dd; }

.glossaryDetail__title {
  color: #6eb3dd; }

.formStatus__step--current {
  background-color: #2c83ba; }
  .formStatus__step--current:not(:last-child)::after {
    border-left-color: #2c83ba; }

.formContainer__title {
  border-left-color: #2c83ba; }

.formContainer__agreement {
  background-color: #eaf4fa; }

.formTable th {
  background-color: #eaf4fa; }
